.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 5rem;
  background-color: #1f1f42;
}
.header.fixed {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  box-shadow: 0.1rem 0.2rem 0.3rem #000000;
}
.header h1 {
  color: #ffffff;
  text-align: center;
  margin: 0;
}

.nav {
  display: flex;
  flex-direction: row;
  gap: 2rem;
}
.nav a {
  display: block;
  text-align: center;
  color: #ffffff;
  font-size: 2rem;
}
.nav a:hover {
  transition: color 0.3s ease;
  color: #ffc164;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5)) 0%, rgba(255, 0, 110, 0.3) 100%;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}
.video .video-content {
  z-index: 1;
}
.video .video-content h2 {
  color: #ffffff;
  font-size: 7rem;
  margin: 2rem 0;
}
.video .video-content p {
  background-color: #866632;
  display: inline-block;
  padding: 1rem 2rem;
  color: #ffffff;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
  border-radius: 1rem;
  user-select: none;
}

.festival {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
  padding: 5rem 0;
}

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.festival-content h2 {
  margin-bottom: 2rem;
}
.festival-content .date {
  color: #866632;
  font-weight: 700;
}

.lineup {
  background-color: #1f1f42;
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

.stage-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
}
.stage-grid .stage {
  padding-top: 2rem;
  box-shadow: 0.1rem 0.2rem 0.3rem #555555;
}
.stage-grid .stage img {
  margin: 2rem auto 2rem;
  width: 15rem;
}
.stage-grid p {
  color: #000000;
}

.calendar li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.background-blue {
  background-color: #9894B2;
}
.background-blue li:nth-child(odd) {
  background-color: #8f8bac;
}

.background-yellow {
  background-color: #ffc164;
}
.background-yellow li:nth-child(odd) {
  background-color: #ffbb55;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 2rem;
}
.gallery-images img {
  cursor: pointer;
  border-radius: 0.5rem;
  transition: transform 0.3s ease;
}
.gallery-images img:hover {
  transform: scale(1.1);
}

.overlay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
.overlay img {
  display: block;
  width: 70%;
}

.tickets {
  padding: 5rem 0;
  background-color: #1f1f42;
}
.tickets h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 2rem;
}
.tickets .tickets-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}

.pass {
  color: black;
}
.pass .pass-name {
  text-align: center;
  font-size: 2.8rem;
  color: #ffffff;
}
.pass-header, .pass-footer {
  padding: 0.5rem;
}
.pass-header p, .pass-footer p {
  text-align: center;
  color: #ffffff;
  margin: 0;
}
.pass-header {
  background-color: #866632;
}
.pass-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass-body {
  background-color: #ffffff;
}
.pass-body ul li,
.pass-body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 1.3rem;
  border-bottom: 1px solid #e1e1e1;
  text-align: center;
}
.pass-footer {
  background-color: #866632;
}
.pass-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.footer {
  background-color: #15152b;
  padding: 3rem;
}
.footer p {
  margin: 0;
  text-align: center;
  font-weight: 700;
  color: #ffffff;
}

@media only screen and (width <= 992px) {
  .container {
    width: 90%;
  }
  .festival {
    display: flex;
    flex-direction: column;
  }
  .gallery-images {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media only screen and (width <= 576px) {
  .container {
    width: 90%;
  }
  .header {
    display: flex;
    flex-direction: column;
  }
  .header h1 {
    margin-bottom: 3rem;
  }
  .header.fixed {
    position: absolute;
  }
  .nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .video .video-content {
    width: 80%;
  }
  .video .video-content h2 {
    font-size: 4rem;
  }
  .video .video-content p {
    font-size: 1.5rem;
    padding: 1rem 1.5rem;
  }
  .stage-grid {
    display: flex;
    flex-direction: column;
  }
  .gallery-images {
    grid-template-columns: repeat(2, 1fr);
  }
  .overlay img {
    width: 100%;
  }
  .tickets .tickets-grid {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  .footer p {
    font-size: 1.5rem;
  }
}